@import url(//fonts.googleapis.com/earlyaccess/notosanstc.css);
body * {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  -o-box-sizing: border-box;
  -ms-box-sizing: border-box;
}
header.header-initial {
  display: block;
  width: 80%;
  height: 500px;
  margin: 0 auto;
}
header.header-initial .logo-initial {
  font-family: 'Permanent Marker', cursive;
  font-size: 56px;
  font-weight: 600;
  height: 280px;
  line-height: 420px;
  display: block;
  text-align: center;
}
header.header-initial .search-area-initial {
  width: 600px;
  margin: 0 auto;
  height: 44px;
  -webkit-box-shadow: 0 2px 2px 0 rgba(0, 0, 0, 0.16), 0 0 0 1px rgba(0, 0, 0, 0.08);
          box-shadow: 0 2px 2px 0 rgba(0, 0, 0, 0.16), 0 0 0 1px rgba(0, 0, 0, 0.08);
}
header.header-initial .search-area-initial .input-initial {
  display: inline-block;
  font-size: 22px;
  width: 550px;
  height: 100%;
  border: none;
  padding-left: 16px;
}
header.header-initial .search-area-initial .input-initial:focus {
  outline: none;
}
header.header-initial .search-area-initial .search-span-initial {
  display: inline-block;
  cursor: pointer;
  float: right;
  width: 50px;
  height: 100%;
  background-image: url('image/search_icon.svg');
  background-repeat: no-repeat;
  background-position: center;
}
header.header-action {
  padding: 10px 0;
  width: 1240px;
  margin:0 auto;
}
header.header-action .logo-action {
  display: inline-block;
  margin: 0 20px;
  font-family: 'Permanent Marker', cursive;
  font-size: 20px;
  min-width:130px;
}
header.header-action .logo-action a{
  color: inherit;
  text-decoration:none;
}
header.header-action .search-area-action {
  display: inline-block;
  -webkit-box-shadow: 0px 0px 1px 0, 0 0 0 0px rgba(0, 0, 0, 0.18);
          box-shadow: 0px 0px 1px 0, 0 0 0 0px rgba(0, 0, 0, 0.18);
}
header.header-action .search-area-action .input-action {
  width: 450px;
  height: 40px;
  padding-left: 16px;
  border: none;
}
header.header-action .search-area-action .input-action:focus {
  outline: none;
}
header.header-action .search-area-action .search-span-action {
  cursor: pointer;
  width: 40px;
  height: 40px;
  float: right;
  background-image: url('./image/search_icon.svg');
  background-position: center;
  background-repeat: no-repeat;
}
header.header-action .now-playing {
  /*in RWD margin-left:0  'Open Sans', 'Roboto',*/
  font-family:  sans-serif;
  float:right;
  color:#666;
  max-width:520px;
  white-space: nowrap;
  -o-text-overflow: ellipsis;
     text-overflow: ellipsis;
  overflow: hidden;
  padding: 0 0 2px 16px;
  margin-right:20px;
  line-height: 40px;
  display: inline-block;
  font-size: 16px;
  font-weight: 600;
  border-left: #666 solid 5px;
}

main {
  display: block;
  width:100%;
}
#playlist-btn{
  position: absolute;
  cursor:pointer;
  top:80px;
  left:50px;
  border:#000 dotted 1px;
  background-color: white;
  padding:10px;
}
#playlist-btn:hover{
  border:#000 solid 1px;
}
main .totalResults{
  letter-spacing: 1px;
  font-size: 14px;
  font-weight: 100px;
  color:#555;
  padding:20px 0 0 6.5px;
  width: 1000px;
  margin: 0 auto;
}

main .playlist-outer{
  position: fixed;
  z-index: 1000;
  top:0;
  left:0;
  display:block;
  width:300px;
  height:100%;
  margin-left:-300px;
  -webkit-transition:all 0.4s linear;
  -o-transition:all 0.4s linear;
  transition:all 0.4s linear;
  background-color: white;
}
main .playlist-outer-active{
  margin-left:0;
}
main .playlist-inner{
  padding:10px;
  width:100%;
  height:100%;
  overflow: auto;
}
main .playlist-article{
  cursor:pointer;
  margin:3px 0;
  width:100%;
  height:60px;
  padding:1px;
}
main .playlist-article:hover{
  padding:0px;
  border:#000 solid 1px;
  height:60px;
}
main .playlist-article .playlist-img{
  display:inline-block;
  width:60px;
  height:60px;
}
main .playlist-article .playlist-title{
  padding:6px;
  font-family: sans-serif;
  float:right;
  font-size: 11px;
  width:200px;
  height:58px;
}

#playlist-close{
  border-radius: 25px;
  position: absolute;
  top:10px;
  right:26px;
  width:50px;
  height:50px;
  cursor:pointer;
  background-image: url("./image/close.svg");
  background-size:50px;
  background-position: center;
  background-repeat:no-repeat;
  background-color: white;
}
#playlist-close:hover{
  background-size:56px;
}

#playlist-play{
  -webkit-transform:rotate(90deg);
      -ms-transform:rotate(90deg);
          transform:rotate(90deg);
  border-radius: 25px;
  position: absolute;
  top:70px;
  right:26px;
  width:50px;
  height:50px;
  cursor:pointer;
  background-image: url("./image/play.svg");
  background-size:50px;
  background-position:center;
  background-repeat:no-repeat;
  background-color: white;
}
#playlist-play:hover{
  background-size:56px;
}
main .error-handle{
  padding:0 20px;
  color:#555;
}
main .arrow{
  position: fixed;
  z-index: 100;
  cursor:pointer;
  border-radius: 100px;
  width:100px;
  height:100px;
  -webkit-filter:opacity(20%);
          filter:opacity(20%);
}
main .arrow:hover{
  position: fixed;
  z-index: 100;
  cursor:pointer;
  border-radius: 100px;
  width:100px;
  height:100px;
  -webkit-filter:opacity(100%);
          filter:opacity(100%);
}
main .arrow-right{
  top:260px;
  right:20px;
}
main .arrow-left{
  -webkit-transform: rotate(180deg);
  -ms-transform: rotate(180deg);
  transform: rotate(180deg);
  top:260px;
  left:20px;
}
#player {
  display:none;
  margin:0 auto;
}
main .search-vid {
  padding:10px 0 50px 0;
  width: 1000px;
  margin: 0 auto;
  overflow: auto;
}
main .search-vid article {
  position: relative;
  width:320px;
  height:180px;
  float: left;
  cursor: pointer;
  margin: 6.5px;
  display: inline-block;
}
main .search-vid article img{
  -webkit-animation-duration: 1.5s;
  animation-duration: 1.5s;
  -webkit-animation-fill-mode: both;
  animation-fill-mode: both;
  -webkit-animation-name: flasheffect;
          animation-name: flasheffect;
}
@-webkit-keyframes flasheffect {
  0%{
     opacity: 0;
  }
}
@keyframes flasheffect {
  0%{
     opacity: 0;
  }
}

main .search-vid article div.overlay {
  position: absolute;
  top:0;
  left:0;
  z-index: 10;
  background-color: rgba(255,255,255,0);
  width:100%;
  height:100%;
}

main .favorite{
  z-index: 11;
  cursor:pointer;
  position: absolute;
  top:0;
  right:0;
  background-image: url("./image/favorite.svg");
  background-repeat: no-repeat;
  background-size: 50px 50px;
  background-position: 5px 0 ;
  width:50px;
  height:50px;
  background-color: white;
  border-bottom-left-radius: 40px;
  -webkit-filter:opacity(50%);
          filter:opacity(50%);
}
main .favorite:hover{
  background-color: #ffd700;
  -webkit-filter:opacity(100%);
          filter:opacity(100%);
}
main .search-vid article .video-info {
  position: absolute;
  bottom: 0;
  left: 0;
  width:100%;
  background-color: rgba(100, 100, 100, 0.9);
}
main .search-vid article .video-info span {
  font-family:  sans-serif;
  letter-spacing:0.5px;
  padding: 2px 6px;
}
main .search-vid article .video-info .title {
  font-size: 17px;
  display: block;
  color: white;
}
main .search-vid article .video-info .author {
  display:inline-block;
  width:240px;
  white-space: nowrap;
  -o-text-overflow: ellipsis;
     text-overflow: ellipsis;
  overflow: hidden;
  font-size: 13px;
  color: #66ff99;
}
main .search-vid article .video-info .duration {
  background-color:black;
  color:white;
  float: right;
  font-size:12px;
}
footer {
  position: absolute;
  bottom:0;
  left:0;
  background-color: transparent;
  width: 100%;
}
footer h6 {
  letter-spacing: 1px;
  font-family: sans-serif;
  line-height: 30px;
  text-align: center;
}



